import React from 'react';
import ReactDOM from 'react-dom'
import AnchorLink from 'react-anchor-link-smooth-scroll'
import {Animated} from "react-animated-css";

import '../Styles/App.css'
import Avatar from '../images/myAvatar.png'


export default function Landing() {
  

  return (
      <div class="animated infinite bounce delay-2s" className='landing-div'>
      <Animated animationIn="fadeInDown" isVisible={true}>
      <div  className='landing-card'>
      <img src={Avatar}></img>
   <p>Welcome to my personal website and portfolio!</p>
   <AnchorLink href='#body-1'>
   <button type="button" class="btn btn-primary">Click here to find out more about me</button>
</AnchorLink>
   </div>
   </Animated>
    </div>
  );
}
