import React from 'react'
import '../Styles/App.css'
import {
    Card, CardImg, CardText, CardBody,
    CardTitle, CardSubtitle, Button
  } from 'reactstrap';
import first from '../images/first.svg'
import {Animated} from "react-animated-css";
import second from '../images/second.svg'

const Body = () => {
    return(
        <div className='body-container'>
            <div className='body-1' id='body-1'>
            
            <div className='card'>
            <Card body inverse color="success">
            <Animated animationIn="flipInY" animationInDelay='1000' >
            <CardImg  className='card-image'  top width="100%" src={first} alt="Card image cap" />
            </Animated>
        <CardTitle className='card-title'>Designer</CardTitle>
        <CardText className='card-text'>With supporting text below as a natural lead-in to additional content.</CardText>
        <Button color="secondary">Button</Button>
      </Card>
</div>
            <div className='card'>  
            <Card body inverse color="info">
            <Animated animationIn="flipInY" animationInDelay='1000' >
            <CardImg className='card-image' top width="100%" src={second} alt="Card image cap"  />
            </Animated>
        <CardTitle className='card-title'>Front-end Developer</CardTitle>
        <CardText className='card-text'>With supporting text below as a natural lead-in to additional content.</CardText>
        <Button color="primary">Button</Button>
      </Card>
      </div>

            <div  className='card'>
      <Card body inverse style={{ backgroundColor: '#333', borderColor: '#333' }}>
      <Animated animationIn="flipInY" animationInDelay='1000' >

      <CardImg className='card-image'  top width="100%" src={first} alt="Card image cap" />
      </Animated>
              <CardTitle className='card-title'>Background</CardTitle>
        <CardText className='card-text'>With supporting text below as a natural lead-in to additional content.</CardText>
        <Button>Button</Button>
      </Card>
      </div>
            </div>

            <div className='body-2' id='body-2'>

            </div>
        </div>
    )
}

export default Body